*, *::after, *::before {
    box-sizing: border-box;
}

body {
    font-family: "Inter", sans-serif;
    margin: 0;
    background-color: #23252C;
}

#root {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.app-container {
    max-width: 317px;
    margin: 0 auto;
    background-color: #F5F5F5;
    border-radius: 10px;
}


.info {

    .profile-img {
        width: 100%;
        height: 317px;
        object-fit: cover;
        object-position: 0 -30px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }

    &--text-content {
        padding: 20px 35px 25px;
        text-align: center;
    }

    &--name,
    &--role,
    &--website,
    &--buttons {
        margin: 0;
    }

    &--name {
        font-size: 1.56rem;
        color: #2B283A;
        margin-bottom: 6px;
    }

    &--role {
        font-size: 0.8rem;
        color: #F47D27;
        margin-bottom: 8.8px;
    }

    &--website {
        display: block;
        font-size: 0.64rem;
        text-decoration: none;
        color: #4A4E74;
        margin-bottom: 17px;

        &:hover,
        &:focus {
            color: #297FE5;
        }
    }

    &--buttons {
        display: flex;
        justify-content: center;
        gap: 17px;
    }

    .social-btn {
        padding: 9px 25px;
        color: #374151;
        background-color: #FFFFFF;
        border: none;
        border: 1px solid #D1D5DB;
        border-radius: 6px;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
        font-size: 0.875rem;
        font-weight: 500;
        cursor: pointer;

        &:hover,
        &:focus {
            background: #F47D27;
            color: #FFFFFF;
        }

        &__icon {
            margin-right: 8px;
        }

        &--accent {
            border: none;
            background-color: #297FE5;
            color: #FFFFFF;
        }

    }
}

.about,
.interests {
    margin: 0;
    padding: 0 35px 25px;
}

.heading {
    margin: 0;
    margin-bottom: 5px;
    font-size: 1rem;
    font-weight: 700;
    color: #2B283A;
}

.text-body {
    margin: 0;
    font-size: 0.64rem;
    font-weight: 400;
    color: #4A4E74;
    line-height: 15px;
}

.footer {
    background-color: #D5D4D8;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

    &__icon {
        color: #918E9B;
        padding-block: 19.5px;
        font-size: 25px;
        cursor: pointer;

        &:hover,
        &:focus {
            color: #F47D27;
        }
    }
}